:root {
  --color-body: #f0f3ff;
  --color-container: #ffffff;
  --color-heading: #2e282c;
  --color-regular-text: #2e282c;
  --color-card-text: #ffffff;

  --font-family-avenir: "Avenir", Arial, Helvetica, sans-serif;

  --font-size-large: 4.8rem;
  --font-size-small: 2.4rem;
  --font-size-filters-320: 3.6rem;

  --line-height-large: 6.6rem;
  --line-height-small: 3.3rem;

  --border-radius-large: 1.6rem;
  --border-radius-small: 0.6rem;
}

html {
  font-size: 10px;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: var(--font-family-avenir);
  background-color: var(--color-body);
}

h1 {
  font-style: normal;
  font-weight: 900;
  font-size: var(--font-size-large);
  line-height: var(--line-height-large);
  color: var(--color-heading);
  margin: 0;
}

p,
span,
label,
select,
option {
  font-family: var(--font-family-avenir);
  font-style: normal;
  font-weight: 500;
  font-size: var(--font-size-small);
  line-height: var(--line-height-small);
  color: var(--color-regular-text);
}

select {
  border: 1px solid #444444;
  border-radius: var(--border-radius-small);
}

input {
  margin: 0;
}

#container {
  margin: 8rem;
  padding: 10rem 10rem 8rem 10rem;
  box-shadow: 0px 16px 32px rgba(0, 0, 0, 0.12);
  border-radius: var(--border-radius-large);
  background-color: var(--color-container);
}

#container > header {
  margin-bottom: 4rem;
}

#container > main > *:nth-child(1) {
  margin-bottom: 4.2rem;
}

@media only screen and (max-width: 1200px) {
  #container {
    margin: 4rem;
    padding: 5rem 5rem 4rem 5rem;
  }
}

@media only screen and (max-width: 960px) {
  html {
    font-size: 8px;
  }
}

@media only screen and (max-width: 760px) {
  html {
    font-size: 6px;
  }
}

@media only screen and (max-width: 480px) {
  html {
    font-size: 8px;
  }

  #container {
    margin: 0;
    border-radius: 0;
    padding: 4rem;
  }
}

@media only screen and (max-width: 320px) {
  html {
    font-size: 6px;
  }
}
